/* PROJECT */
.projects {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100vh;
  position: relative;
  font-size: 16px;
}
.projects__list {
  list-style-type: none;
  width: 100%;
  height: 100%;
}
.projects__item {
  height: 100%;
  background-image: url(../img/fogg-page-not-found-1.png);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  background-size: contain;
  font-size: 1.7em;
}
